import { Component, h, State, Event, EventEmitter  } from '@stencil/core';

@Component({
  tag: 'rf-guest',
  styleUrl: './guest.css',
  shadow: true,
})
export class Guest {
  @State() counter = 0;
  increment() {
    this.counter++;
    this.send_guest.emit(this.counter)
  }
  decrement() {
    if (this.counter > 1) {
      this.counter--;
    }
    this.send_guest.emit(this.counter)
  }

  @Event({ bubbles: true, composed: true }) send_guest: EventEmitter<number>;
  render() {
    return (
      <div class="container">
        <div>{this.counter}</div>
        <div>{this.counter == 1 ? 'guest' : 'guests'}</div>
        <div class="wrap">
          <button onClick={this.increment.bind(this)}>+</button>
          <button onClick={this.decrement.bind(this)}>-</button>
        </div>
      </div>
    );
  }
}
